/* Stats */
.stats {
	margin: 10px;
}

.stats table {
	margin: auto;
}

.stats th {
	text-align: right;
	font-weight: normal;
	letter-spacing: 2px;
	font-size: .7em;
}

.stats tr:nth-child(2) td {
	color: indianred;
}

.stats tr:last-child td {
	color: darkgoldenrod;
}

.stats td {
	text-align: left;
	font-size: 1.5rem;
	font-family: monospace;
}



/* Stopwatch */
.stopwatch {
	padding: 15px 10px 5px 10px;
}

.stopwatch-time {
	font-family: monospace;
	font-size: 2em;
}

.stopwatch-controls button {
	margin: 5px;
	color: white;
}


/* Todo */
.todo {
	display: -ms-flexbox;
	display: flex;
	font-size: 1rem;
	border-top: 1px solid rgba(255,255,255,.1);
	background-color: #2b2b2b;
	-webkit-transition: all .2s;
	-o-transition: all .2s;
	transition: all .2s;
	-webkit-user-select: none;
	-moz-user-select: none;
	 -ms-user-select: none;
	     user-select: none;
	padding: 1em;
	-ms-flex-align: center;
	    align-items: center;
}

.todo.completed * {
	color: indianred;
}

.todo.completed .todo-title {
	text-decoration: line-through;
}

.todo .todo-title {
	margin-right: auto;
}

.todo .checkbox {
	margin-right: .5rem;
}

.todo button:not(.checkbox) {
	opacity: 0;
	-webkit-transition: all .2s;
	-o-transition: all .2s;
	transition: all .2s;
}

.todo:hover button:not(.checkbox) {
	opacity: 1;
}

.todo-edit-form {
	display: -ms-flexbox;
	display: flex;
	font-size: 1rem;
	padding: .85em 10px;
	background-color: #222;
	border-top: 1px solid rgba(0,0,0,.1);
}

.todo-edit-form input {
	-ms-flex: 1 1;
	    flex: 1 1;
	background-color: #111;
	border-color: currentColor;
}

.todo-edit-form .save {
	margin-left: .5em;
}

/* Todo form */

.todo-form {
	display: -ms-flexbox;
	display: flex;
	background-color: #222;
	border-top: 1px solid rgba(0,0,0,.1);
	padding: 10px;
}

.todo-form input {
	-ms-flex: 1 1;
	    flex: 1 1;
	outline: none;
	-webkit-transition: all .2s;
	-o-transition: all .2s;
	transition: all .2s;
	background-color: #111;
	border-color: currentColor;
}

.todo-form input:focus {
	border: 1px solid darkgoldenrod;
}

.todo-form button {
	margin-left: 10px;
}
